
import {SetupContext, defineComponent, watch, onUnmounted, onMounted, getCurrentInstance, nextTick} from "vue"
import FormButton from "@/components/forms/Button.vue"
export default defineComponent({
  components: {
    FormButton,
  },
  name: "DialogModal",
  props: {
    title: {
      type: String,
    },
    size: {
      type: String,
      default: 'sm',
      // xs sm md lg xl fl
    },
    close: {
      type: Boolean,
      default: false
    },
    submitting: {
      type: Boolean,
      default: false
    },
    onClose: {
    },
    onConfirm: {
    },
    onCancel: {
    },
    onSubmit: {
    },
  },
  setup(props: any, context: SetupContext) {
    const internalInstance = getCurrentInstance() // works
    const onClose2 = () => {
      context.emit("close")
    }
    const onConfirm2 = () => {
      context.emit("confirm")
    }
    const onCancel2 = () => {
      context.emit("cancel")
    }
    watch(()=> props.close, function() {
      
      nextTick(function() {
        // @ts-ignore
        internalInstance.ctx.$el.focus()
      })
    })
    
    
    return {
      onConfirm2,
      onCancel2,
      onClose2,
    }
  }
})
